<template>
    <div>
        <s-header :title="title" isBack></s-header>
        <s-main>
            <div class="s-detail" v-show="content" v-html="content" @click="setting"></div>
        </s-main>
        <div class="s-setting" v-show="show"></div>
    </div>
</template>

<script>
    export default {
        name: "Detail",
        data() {
            return {
                bid: this.$route.query.bid,
                id: this.$route.query.pid,
                title: this.$route.query.title,
                content: '',
                show: false
            }
        },
        mounted() {
            this.getDetail();
        },
        methods: {
            getDetail: function () {
                let params = {
                    bid: this.bid,
                    pid: this.id
                };
                this.$api.detail(params).then(res => {
                    if (res.code === 0) {
                        this.content = res.data.content.replace(/\s+/g, '<p>');
                    }
                });
            },
            setting: function () {
                // this.show = !this.show;
            }
        }
    }
</script>

<style lang="less" scoped>
    .s-detail {
        padding: .1rem;
        font-size: .18rem;
        color: #333;
        line-height: 180%;
        /deep/ p {
            padding: .1rem 0;
        }
    }
    .s-setting {
        height: 1rem;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        background: #d1cfa9;
    }
</style>